.search {
	&:not(:first-child) {
		margin-top: 20px; }
	&__decription {
		font-size: 24px;
		@include md {
			font-size: 20px; }
		@include sm {
			font-size: 17px; } }
	&__form {
		display: flex;
		&:not(:first-child) {
			margin-top: 10px; } }
	&__control {
		flex-grow: 1;
		input {
			padding: 10px 15px;
			width: 100%;
			height: 60px;
			font-family: $sourcecodepro;
			font-size: inherit;
			border: 1px solid #D1D1D1;
			&:focus {
				&::placeholder {
					font-size: 0; } } } }
	&__btn {
		@include sm {
			min-width: 150px; } }
	&__field {
		padding: 15px;
		min-height: 400px;
		text-align: left;
		@include prevent-text;
		border: 1px solid #D1D1D1;
		@include md {
			min-height: 300px; }
		@include sm {
			min-height: 200px; }
		&:not(:first-child) {
			margin-top: 30px; } } }

.btn {
	padding: 20px 30px;
	min-width: 230px;
	font-family: $sourcecodepro;
	@include f(16px,700,1.3);
	color: $white;
	text-align: center;
	user-select: none;
	transition: background-color .3s ease-in-out;
	background-color: $black;
	display: inline-block;
	@include hover {
		background-color: lighten($black, 20%); } }
