body {
	min-width: 320px;
	font-family: $sourcecodepro;
	@include f(22px,300,1.4);
	@include font-smoothing;
	background: $white;
	line-height: 1.4;
	@include md {
		font-size: 18px; }
	@include sm {
		font-size: 16px; } }


.title {
	margin-bottom: 15px;
	@include f(62px,600,1.2);
	@include md {
		font-size: 52px; }
	@include sm {
		font-size: 38px; } }

p + p {
	margin-top: 10px; }
