body {
  min-width: 320px;
  font-family: "Sourcecodepro", Open Sans, sans-serif;
  font-size: 22px;
  font-weight: 300;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
  line-height: 1.4;
}

@media only screen and (max-width: 1023px) {
  body {
    font-size: 18px;
  }
}

@media only screen and (max-width: 767px) {
  body {
    font-size: 16px;
  }
}

.title {
  margin-bottom: 15px;
  font-size: 62px;
  font-weight: 600;
  line-height: 1.2;
}

@media only screen and (max-width: 1023px) {
  .title {
    font-size: 52px;
  }
}

@media only screen and (max-width: 767px) {
  .title {
    font-size: 38px;
  }
}

p + p {
  margin-top: 10px;
}

.inner {
  padding: 0 15px;
  margin: 0 auto;
  max-width: 1200px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.header {
  height: 105px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.main {
  padding: 150px 0;
  text-align: center;
}

@media only screen and (max-width: 1023px) {
  .main {
    padding: 80px 0;
  }
}

@media only screen and (max-width: 767px) {
  .main {
    padding: 50px 0;
  }
}

.search:not(:first-child) {
  margin-top: 20px;
}

.search__decription {
  font-size: 24px;
}

@media only screen and (max-width: 1023px) {
  .search__decription {
    font-size: 20px;
  }
}

@media only screen and (max-width: 767px) {
  .search__decription {
    font-size: 17px;
  }
}

.search__form {
  display: flex;
}

.search__form:not(:first-child) {
  margin-top: 10px;
}

.search__control {
  flex-grow: 1;
}

.search__control input {
  padding: 10px 15px;
  width: 100%;
  height: 60px;
  font-family: "Sourcecodepro", Open Sans, sans-serif;
  font-size: inherit;
  border: 1px solid #D1D1D1;
}

.search__control input:focus::placeholder {
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .search__btn {
    min-width: 150px;
  }
}

.search__field {
  padding: 15px;
  min-height: 400px;
  text-align: left;
  word-break: break-all;
  word-break: break-word;
  hyphens: auto;
  border: 1px solid #D1D1D1;
}

@media only screen and (max-width: 1023px) {
  .search__field {
    min-height: 300px;
  }
}

@media only screen and (max-width: 767px) {
  .search__field {
    min-height: 200px;
  }
}

.search__field:not(:first-child) {
  margin-top: 30px;
}

.btn {
  padding: 20px 30px;
  min-width: 230px;
  font-family: "Sourcecodepro", Open Sans, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  color: #fff;
  text-align: center;
  user-select: none;
  transition: background-color .3s ease-in-out;
  background-color: #000;
  display: inline-block;
}

.no-touch .btn:hover {
  background-color: #333333;
}

.footer {
  padding: 90px 0;
  color: #fff;
  text-align: center;
  background-color: #000;
}

@media only screen and (max-width: 1023px) {
  .footer {
    padding: 60px 0;
  }
}

@media only screen and (max-width: 767px) {
  .footer {
    padding: 50px 0;
  }
}

.footer__logo {
  max-width: 300px;
  vertical-align: top;
  display: inline-block;
}

.footer__logo img {
  max-width: 100%;
  display: block;
}

.footer__field {
  margin-top: 30px;
}

.footer__link {
  color: #fff;
  transition: border-color .3s;
  border-bottom: 1px solid transparent;
}

.no-touch .footer__link:hover {
  border-color: currentColor;
}
